/**
 * @file global css
 * @author chenwen(chenwen@baijiahulian.com)
 */
html,
body,
h1,
h2,
h3,
ul,
pre,
p {
  margin: 0;
  padding: 0;
}
html {
  height: 100%;
}
body {
  height: 100%;
  font-family: PingFangSC-Regular, "Hiragino Sans GB", "Helvetica Neue", "", "Microsoft Yahei", Helvetica, Arial, sans-serif;
  background-color: #fff;
  color: #333;
  font-size: 14px;
  background-color: #F7F7F7;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
}
body input, body select, body textarea, body button, body pre {
    font-family: PingFangSC-Regular, "Hiragino Sans GB", "Helvetica Neue", "", "Microsoft Yahei", Helvetica, Arial, sans-serif;
}
body input::-moz-placeholder,
  body textarea::-moz-placeholder {
    color: #999 !important;
    opacity: 1;
}
body input:-ms-input-placeholder,
  body textarea:-ms-input-placeholder {
    color: #999 !important;
}
body input::-webkit-input-placeholder,
  body textarea::-webkit-input-placeholder {
    color: #999 !important;
}
h2 {
  font-size: 14px;
  line-height: 1;
}
h3 {
  font-size: 13px;
  line-height: 1;
}
h1 {
  font-size: 14px;
  line-height: 30px;
  font-weight: normal;
}
ul li {
  list-style: none;
}
a {
  cursor: pointer;
  color: #46a0f0;
  text-decoration: none;
  font-weight: normal;
}
a:focus, a:active {
    border: 0;
    outline: none;
}
textarea {
  overflow: auto;
}
.vertical-middle, .import-dialog .complete span,
.middle {
  vertical-align: middle;
  display: inline-block;
}
.none-list {
  text-align: center;
  padding: 3rem;
  color: #999;
  min-width: 98px;
  min-height: 98px;
}
.none-list:before {
    display: block;
    content: '';
    width: 98px;
    height: 98px;
    background: url("assets/img/none-data-bg.png") no-repeat;
    background-size: contain;
    margin: 30px auto;
}
.btn {
  display: inline-block;
  box-sizing: border-box;
  padding: 7px 15px;
  text-align: center;
  font-size: 14px;
  color: #333;
  line-height: 1;
  border: 1px solid #ddd;
  border-radius: 4px;
  background-color: #fff;
  outline: none;
  cursor: pointer;
}
.btn:hover {
    opacity: .8;
}
.btn-disable {
    background-color: #eef1f6;
    border-color: #d1dbe5;
    color: #bfcbd9;
    cursor: not-allowed;
}
.btn-disable:hover {
    opacity: 1;
}
.btn-blue-light {
    background-color: #fff;
    color: #46a0f0;
    border-color: #46a0f0;
}
.btn-blue-light:hover {
      border-color: #3e96e5;
      color: #3e96e5;
}
.btn-primary {
    background-color: #46a0f0;
    border-color: #46a0f0;
    color: #fff;
}
.btn + .btn {
    margin-left: 6px;
}
.clearfix:before, .clearfix:after {
  content: " ";
  display: table;
}
.clearfix:after {
  clear: both;
}
.pointer {
  cursor: pointer;
}
.clickable-text, .main td.clickable-td .cell span {
  color: #666;
  cursor: pointer;
}
.clickable-text:hover, .main td.clickable-td .cell span:hover {
    color: #3e96e5;
}
.clickable-text-blue, .main td.clickable-td .cell, .main td.clickable-td .cell span {
    color: #46a0f0;
}
.text-success {
  color: #1abc9c;
}
.text-yellow {
  color: #f7ba2a;
}
.text-danger {
  color: #ff4949;
}
.text-gray-light {
  color: #999;
}
.text-light-color {
  color: #999;
}
.text-red {
  color: #ff4949;
}
.pull-left {
  float: left;
}
.pull-right {
  float: right;
}
pre {
  white-space: -moz-pre-wrap;
  white-space: -pre-wrap;
  white-space: -o-pre-wrap;
  white-space: pre-wrap;
  word-wrap: break-word;
}
.confirm-message-box {
  padding-bottom: 0;
}
.confirm-message-box .el-message-box__header {
    display: none;
}
.confirm-message-box .el-message-box__content {
    padding: 30px;
}
.confirm-message-box .el-message-box__content .el-message-box__status {
      top: auto;
      -webkit-transform: none;
              transform: none;
}
.confirm-message-box .el-message-box__content .el-message-box__message p:first-child {
      color: #333;
      font-weight: bold;
}
.confirm-message-box .el-message-box__content .el-message-box__message p:last-child {
      color: #999;
      font-size: 12px;
      margin-top: -1px;
}
.confirm-message-box .el-message-box__btns {
    padding: 10px 30px 20px;
}
.confirm-message-box .el-message-box__btns button {
      font-size: 14px;
}
.course-list {
  *zoom: 1;
}
.course-list:after, .course-list:before {
    content: "";
    display: table;
}
.course-list:after {
    clear: both;
    overflow: hidden;
}
.equipment-guide {
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.equipment-status-list .remark {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.slide-down-enter-active, .slide-down-leave-active {
  transition: all .3s ease;
}
.slide-down-enter-active li, .slide-down-leave-active li {
    transition: all .1s ease;
}
.slider-down-enter-active, .slide-down-leave {
  opacity: 1;
}
.slider-down-enter-active li, .slide-down-leave li {
    opacity: 1;
    height: 44px;
}
.slide-down-leave-active, .slide-down-enter {
  opacity: 0;
}
.slide-down-leave-active li, .slide-down-enter li {
    opacity: 0;
    height: 0;
}
.slide-right-enter-active, .slide-right-leave-active {
  transition: all .5s ease;
}
.slide-right-enter, .slide-right-leave-active {
  position: absolute;
  -webkit-transform: translate3d(2000px, 0, 0);
          transform: translate3d(2000px, 0, 0);
  opacity: .3;
}
.slide-right-leave, .slide-right-enter-active {
  position: absolute;
  opacity: 1;
}
.component-fade-enter-active, .component-fade-leave-active {
  transition: opacity .2s ease;
}
.component-fade-enter, .component-fade-leave-active {
  opacity: 0;
}
.slide-fromright-enter-active, .slide-fromright-leave-active {
  transition: all .3s ease;
  position: absolute;
}
.slide-fromright-enter {
  position: absolute;
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
  opacity: .3;
}
.slide-fromright-enter-active {
  opacity: 1;
}
.slide-fromright-leave-active {
  opacity: 0;
}
.slide-toright-enter-active, .slide-toright-leave-active {
  transition: all .3s ease;
  position: absolute;
  opacity: 1;
}
.slide-toright-enter {
  opacity: .3;
  -webkit-transform: translate3d(0, 20px, 0);
          transform: translate3d(0, 20px, 0);
}
.slide-toright-leave-active {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
@-webkit-keyframes fadeInRight {
from {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0);
}
to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
}
}
@keyframes fadeInRight {
from {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0);
}
to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
}
}
.fadeInRight {
  -webkit-animation-name: fadeInRight;
  animation-name: fadeInRight;
}
@-webkit-keyframes fadeOutLeft {
from {
    opacity: 1;
}
to {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
}
}
@keyframes fadeOutLeft {
from {
    opacity: 1;
}
to {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
}
}
.fadeOutLeft {
  -webkit-animation-name: fadeOutLeft;
  animation-name: fadeOutLeft;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity .2s ease;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
