#sidebar {
  position: fixed;
  top: 60px;
  left: 0;
  bottom: 0;
  z-index: 2000;
  width: 180px;
  height: 100%;
  background-color: #46a0f0;
}
#sidebar .menu {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    /* scrollbar for chrome*/
}
#sidebar .menu::-webkit-scrollbar {
      width: 0;
}
#sidebar .menu:hover::-webkit-scrollbar {
      width: 8px;
}
#sidebar .menu:hover::-webkit-scrollbar-thumb {
      background-color: rgba(255, 255, 255, 0.3);
      background-clip: content-box;
      border-right: 5px solid transparent;
}
#sidebar .menu:hover::-webkit-scrollbar-track {
      background-color: transparent;
}
#sidebar li {
    display: block;
    height: 65px;
    padding: 0 15px;
    line-height: 65px;
    font-size: 18px;
    position: relative;
    color: #fff;
    cursor: pointer;
    transition: all, .5s;
}
#sidebar li .svg-icon,
    #sidebar li span {
      display: inline-block;
      vertical-align: middle;
}
#sidebar li:hover {
      background-color: #348BE5;
}
#sidebar li.active {
      background-color: #348BE5;
}
#sidebar li.active:after {
        position: absolute;
        right: 0;
        top: 50%;
        border: 7px solid transparent;
        border-right-color: #fff;
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
        content: '';
        width: 0;
        height: 0;
}
