.upload-btn .cover-url a {
      background-color: #fff;
      border-radius: 3px;
      font-size: 12px;
      padding: 0 3px;
}
.apply-course-dialog .detail-from .el-form-item {
  margin-bottom: 10px;
}
.apply-course-dialog h2 {
  font-style: 16px;
  color: #46a0f0;
  margin-bottom: 20px;
}
.apply-course-dialog h2.apply-title {
    border-top: 1px solid #DADADA;
    padding-top: 20px;
}
.main {
  box-sizing: border-box;
  min-width: 1265px;
  height: 100%;
  padding: 50px 0 0 180px;
}
.main > div {
    padding: 16px 0px;
    margin: 10px 20px;
}
.main .list-box {
    background: #fff;
    min-height: 300px;
    padding: 10px;
}
.main .module-title {
    font-weight: normal;
    color: #46a0f0;
    font-size: 18px;
    margin-bottom: 16px;
    line-height: 40px;
}
.main .module-title .el-col:first-child .el-breadcrumb__item__inner {
      line-height: 30px;
}
.main .module-title .el-col:nth-child(2) {
      text-align: right;
}
.main .module-title .el-col:nth-child(2) .el-dropdown {
        margin-left: 10px;
}
.main .module-title .btn-group .el-button {
      margin-right: 9px;
}
.main .module-title .btn-group .el-button:last-of-type {
        margin: 0;
}
.main .el-tabs {
    margin: -20px -20px 0;
}
.main .el-tabs .el-tabs__header {
      margin: 0;
}
.main .pagination {
    text-align: right;
    padding-top: 20px;
}
.main .pswp {
    z-index: 4500;
}
.filter-box {
  margin: 10px 0 14px 0;
}
.filter-box > .el-input,
  .filter-box > .el-select {
    margin-right: 14px;
}
.filter-box > .el-input:last-child,
    .filter-box > .el-select:last-child {
      margin-right: 0;
}
.rule-field-popover {
  color: #666;
  line-height: 20px;
}
.second-page-wrap, .material-detail-wrap {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 7;
  box-sizing: border-box;
  padding: 0 0 0 160px;
  margin-top: 50px;
  background-color: #f2f2f2;
  overflow-x: auto;
}
.second-page-wrap .wrap-content, .material-detail-wrap .wrap-content {
    min-width: 1045px;
    padding: 12px 20px;
}
.material-list {
  position: relative;
}
.pager {
  text-align: right;
  margin: 20px 0;
}



