
.set-repeat-wrap > .top {
  padding: 10px 12px;
  border-bottom: 1px solid #ddd;
}
.set-repeat-wrap > .top .btn-content {
    color: #333;
}
.set-repeat-wrap > .top .el-select {
    display: inline-block;
}
.set-repeat-wrap .btn-group > button {
  height: 33px;
}
.set-repeat-wrap .btn-group > button.btn-default {
    background: none;
}
.set-repeat-wrap .btn-group > button.btn-default:focus, .set-repeat-wrap .btn-group > button.btn-default:active {
      box-shadow: none;
      background: none;
      border: 1px solid #ddd;
}
.set-repeat-wrap > .center {
  padding: 15px 15px 3px 15px;
}
.set-repeat-wrap > .center > div {
    margin-bottom: 10px;
}
.set-repeat-wrap > .center .week span {
    display: inline-block;
    width: 30px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    border-left: 1px solid #ddd;
    cursor: pointer;
}
.set-repeat-wrap > .center .week span:last-child {
      border-right: 1px solid #ddd;
}
.set-repeat-wrap > .center .week span.active {
      position: relative;
      color: #fff;
}
.set-repeat-wrap > .center .week span.active::before {
        content: attr(data-text);
        background: #00a7ff;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
}
.set-repeat-wrap > .center input[type="text"] {
    width: 120px;
    border: 1px solid #ddd;
    outline: none;
    padding: 3px 10px;
    height: 30px;
    box-sizing: border-box;
}



