
.down-card {
  background-color: #fff;
  border: 1px solid transparent;
  border-radius: 1px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.1);
  -webkit-border-radius: 1px;
  position: absolute;
  opacity: 1;
  transition: all .2s ease-in-out;
  z-index: 1071;
}
.down-card .bottom {
    margin: 5px 0;
    padding: 5px 0 8px 0;
    text-align: center;
}



