
.upload-wrapper .tip {
  color: #999;
  line-height: 40px;
  float: left;
  height: 40px;
  margin-left: 10px;
}
.upload-wrapper .upload-btn {
  position: relative;
  overflow: hidden;
  float: left;
}
.upload-wrapper .upload-btn .btn {
    font-size: 14px;
    color: #6f7276;
    background: #f6f7f9;
    border: 1px solid #e0e0e0;
    border-radius: 4px;
    width: 158px;
    height: 34px;
    line-height: 34px;
    padding: 0px;
}
.upload-wrapper .upload-btn > div {
    text-align: left;
}
.upload-wrapper .upload-btn input[type="file"] {
    position: absolute;
    top: 0;
    right: 0;
    margin: 0;
    opacity: 0;
    -webkit-transform: translate(-300px, 0) scale(4);
    -webkit-transform: translate(-300px, 0) scale(4);
    transform: translate(-300px, 0) scale(4);
    font-size: 23px;
    direction: ltr;
    cursor: pointer;
}



