
header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9;
  height: 60px;
  line-height: 60px;
  text-align: right;
  z-index: 2003;
  background-color: #fff;
  border-bottom: 1px solid #dfdfdf;
}
header .logo {
    font-size: 24px;
    color: #409EFF;
    padding: 0 10px;
    font-family: PingFangSC-Medium;
}
header .logo img {
      vertical-align: middle;
}
header .user {
    color: #666;
    padding-right: 30px;
}
header .user .name {
      font-size: 16px;
      margin-right: 49px;
}
header .user .exit .svg-icon,
    header .user .exit span {
      display: inline-block;
      vertical-align: middle;
}



